export function timeFormat(time) {
  if (time !== 0 && !time) return false;

  const clock = time > 12 ? time - 12 : time;
  let result = "";

  if (6 <= time && time < 10) {
    result = " " + clock + "";
  } else if (10 <= time && time < 12) {
    result = " " + clock + "";
  } else if (12 <= time && time < 18) {
    result = " " + clock + "";
  } else if (18 <= time && time < 22) {
    result = " " + clock + "";
  } else if ((22 <= time && time <= 23) || time == 0) {
    result = " " + clock + "";
  } else {
    result = " " + clock + "";
  }
  return result;
}
